import React from "react";
import {
  ListContainer,
  WorkedTimerPeriodContainer,
  Time,
  Duration,
  Item,
  Description,
  DescriptionContainer,
  LogoImg,
} from "./Style";

const ExperienceList = (props) => {
  return (
    <>
      {props.list &&
        props.list.map((el, i) => {
          return (
            <ListContainer key={i + 1}>
              <div>
                <LogoImg src={el.icon} alt={el.company} />
              </div>
              <div>
                <WorkedTimerPeriodContainer>
                  <Time>{el.time}</Time>
                  <Duration>{el.duration}</Duration>
                </WorkedTimerPeriodContainer>
                <Item>{el.position}</Item>
                <Item>{el.company}</Item>
                <DescriptionContainer>
                  {
                    <ul>
                      {el.description.map((des, index) => {
                        return <Description>{des}</Description>;
                      })}
                    </ul>
                  }
                </DescriptionContainer>
              </div>
            </ListContainer>
          );
        })}
    </>
  );
};

export default ExperienceList;
