import React from "react";
import Tilt from "react-tilt";
import {
  CardContainer,
  Image,
  ImageBorder,
  Words,
  Name,
  Position,
  ParagraphContainer,
} from "./Style";

const TestimonialCard = ({ imagelink, words, name, position }) => {
  return (
    <CardContainer>
      <Tilt>
        <ImageBorder>
          <Image src={imagelink} />
        </ImageBorder>
      </Tilt>
      <Words>{words}</Words>
      <ParagraphContainer>
        <Name>{name},</Name> <Position>{position}</Position>
      </ParagraphContainer>
    </CardContainer>
  );
};

export default TestimonialCard;
