import React from "react";
import { ListContainer, Icon, Time, Degree, Place } from "./Style";
import { FaSchool } from "react-icons/fa";

const EducationList = (props) => {
  return (
    <>
      {props.list &&
        props.list.map((el, i) => {
          return (
            <ListContainer key={i + 1}>
              <Icon>
                <FaSchool />
              </Icon>
              <div>
                <Time>{el.time}</Time>
                <Degree>{el.degree}</Degree>
                <Place>{el.place}</Place>
              </div>
            </ListContainer>
          );
        })}
    </>
  );
};

export default EducationList;
