import styled from "styled-components";

export const Background = styled.div`
  display: flex;
  background-color: #333c3d;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding-top: 3rem;
  overflow: hidden;
  padding-bottom: 5rem;

  @media (max-width: 480px) {
    padding: 1rem;
  }
`;

export const Header = styled.h3`
  color: #eee;
  font-size: 3.5rem;
  padding-bottom: 3rem;
  letter-spacing: 5px;
  font-weight: bold;
  text-shadow: 2px 2px #292c2a;

  @media (max-width: 480px) {
    font-size: 1.7rem;
    text-align: center;
  }
`;

export const SubHeader = styled.h3`
  color: #9c9999;
  font-size: 2rem;
  padding-bottom: 1rem;
  letter-spacing: 5px;
  font-weight: bold;
  text-shadow: 2px 2px #292c2a;

  @media (max-width: 480px) {
    font-size: 1rem;
    text-align: center;
  }
`;

export const SliderContainer = styled.div`
  width: 50%;

  @media (max-width: 480px) {
    width: 80%;
  }
`;
