import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import Homescreen from "./../Homescreen/Homescreen";
import Aboutscreen from "./../Aboutscreen/Aboutscreen";
import Resumescreen from "./../Resumescreen/Resumescreen";
import Servicescreen from "./../Servicescreen/Servicescreen";
import Projectscreen from "./../Projectscreen/Projectscreen";
import Contactscreen from "./../Contactscreen/Contactscreen";
import FooterBranding from "./../FooterBranding/FooterBranding";
import Testimonialscreen from "./../Testimonialscreen/Testimonialscreen";

const App = (props) => {
  useEffect(() => {
    AOS.init({
      duration: 200,
    });
  }, []);

  return (
    <div>
      <Homescreen />
      <Aboutscreen />
      <Resumescreen />
      <Servicescreen />
      <Projectscreen />
      <Testimonialscreen />
      <Contactscreen />
      <FooterBranding />
    </div>
  );
};

export default App;
