import React from "react";
import Box from "./components/Box/Box";
import witw from "./../staticFiles/projects/Whereintheworld-min.png";
import tmdb from "./../staticFiles/projects/tmdbMovies-min.png";
import trillo from "./../staticFiles/projects/trillo-min.png";
import vali from "./../staticFiles/projects/validator-min.png";
import nato from "./../staticFiles/projects/natours-min.png";
import nexter from "./../staticFiles/projects/nexter-min.png";
import { Background, Header, Desc, BoxContainer } from "./Style";

const Projectscreen = () => {
  return (
    <Background name="section-Project">
      <Header>Project</Header>
      <Desc>Here are some projects I've created or been a part of</Desc>
      <div>
        <BoxContainer>
          <Box
            src={witw}
            alt={"witw"}
            to="https://whereinthecountry.netlify.app"
          />
          <Box src={tmdb} alt={"tmdb"} to="https://tmdbmovie.herokuapp.com/" />
          <Box
            src={trillo}
            alt={"trillo"}
            to="https://gadgetvala.github.io/Trillo-frontend/"
          />
        </BoxContainer>
        <BoxContainer>
          <Box
            src={vali}
            alt={"vali"}
            to="https://gadgetvala.github.io/validator/"
          />
          <Box
            src={nato}
            alt={"nato"}
            to="https://gadgetvala.github.io/Natour-frontend/"
          />
          <Box
            src={nexter}
            alt={"nexter"}
            to="https://gadgetvala.github.io/Nexter-frontend/"
          />
        </BoxContainer>
      </div>
      <Desc top>
        <a href="https://github.com/gadgetvala">CLICK HERE</a>
        to Checkout More cool Projects
      </Desc>
    </Background>
  );
};

export default Projectscreen;
